 
import styled from 'styled-components';
import { backgroundColor } from '../color';

const ButtonStyled = styled.button`
  /* Adapt the colors based on primary prop */
  background: ${props => props.color ? backgroundColor.fb305a : "white"};
  color: ${props => props.color ? "white" : "palevioletred"};

  font-size: 1em;
  margin: 1em;
  padding: 0.25em 1em; 
  border: none;
  border-radius: 3px;
`;
type ButtonProps ={
    color?: string;
    children: React.ReactNode;
    onclick?: ()=>void
}
export const Button = ({children, color = '', onclick}:ButtonProps): JSX.Element => {
  
    return ( 
        <ButtonStyled color={color} onClick={onclick}>{children}</ButtonStyled> 
     
    )
} 