 
import {Button} from '../componets/Button'
import { useState, useEffect } from 'react'
import styled from 'styled-components';
import "./Home.css";
import apiClient  from "../api/axios";
import {apiLink}  from "../api/apiConstant";
import {  useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from 'react-redux';
import { bindActionCreators } from 'redux'; 
import { actionCreators, State } from '../state';
 
const StyledDiv = styled.div`
padding: 3rem 1.5rem;
border-radius: 1rem; 
`;

const StyledP = styled.p`
font-size: .7rem;
text-align: right;
color:orange;
margin: 0px;
padding-right: 5px;
position: relative;
top:-5px;
`;
 

const Home = ()=>{
  
  const [userName, setuserName] = useState('');
 
  const [errorFlg, seterrorFlg] = useState(false);
    const [username, setUserName] = useState('');
    const [phone, setPhone] = useState('');
    const navigate= useNavigate();
    const dispatch = useDispatch(); 
    const { getUserName } = bindActionCreators(
      actionCreators,
      dispatch
    );
  
  const loginHandler = ()=>{
    if(username.length !== 0 && phone.length === 10){          
      onclickHandler();       
    }else{
      seterrorFlg(true)
    }
  }

  useEffect(()=>{
 
    //localStorage.removeItem('username');
    const storedName = localStorage.getItem('username');
    if(storedName){
      navigate('/dashboard')
    }

  },[]);
   
   
  async function onclickHandler() { 
  
      const url=`${apiLink}addUser`
      
      let data={
        username,
        email:"NA",
        phone,
        referplayers:1,
        paidamount:0,

      };
 
   
      // Default options are marked with *
      const response = await fetch(url, {
        method: 'POST', // *GET, POST, PUT, DELETE, etc.
        mode: 'no-cors', // no-cors, *cors, same-origin
        cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
        credentials: 'same-origin', // include, *same-origin, omit
        headers: {
          'Content-Type': 'application/json'
          // 'Content-Type': 'application/x-www-form-urlencoded',
        },
        redirect: 'follow', // manual, *follow, error
        referrerPolicy: 'no-referrer', // no-referrer, *no-referrer-when-downgrade, origin, origin-when-cross-origin, same-origin, strict-origin, strict-origin-when-cross-origin, unsafe-url
        body: JSON.stringify(data), // body data type must match "Content-Type" header
      }) 
      .then((res) => res)
      .then((post) => { 
        getUserName(data.username);
        localStorage.setItem('username', data.username); 
        navigate('/dashboard'); 
    
      })
      .catch((err) => {
         console.log(err);
      }); 
      setUserName('')
      //setPhone('')
      seterrorFlg(false); 
     return response; // parses JSON response into native JavaScript objects
    }

   
    
  const onChangeHandler = (e: any)=>{
    setUserName(e.target.value)
  }
  const onChangePhone = (e:any) =>{
    setPhone(e.target.value)
  }
 
  
    return ( 
        
        <div className="flex-container">
           
        <StyledDiv className='divCls'>
            <div className='loginDiv'>
            <input className='m-1' placeholder='your name'
            onChange={onChangeHandler} value={username}/>  
            {(username.length === 0 && errorFlg ) && 
            <StyledP>please give username</StyledP>}
            <input  className='m-1' maxLength={10}  type="number"  placeholder='your phone no'
            onChange={onChangePhone} value={phone}/> 
            {(phone.length !== 10 && errorFlg ) && 
            <StyledP>incorrect phone no - (10)</StyledP>} 
            <Button color='primary' 
            onclick={loginHandler}
            >I'm iN</Button> 
            </div>
         
        </StyledDiv>
         
      </div>
    )
}
export default Home